import { Col, Row, Carousel } from "react-bootstrap";
import "./About.css";
import datum from "../../Assets/data";
const About = () => {
  return (
    <>
      <Row className="text-light d-flex flex-nowrap align-items-center row-content gap-5 about">
        <Col>
          <Carousel>
            {datum.map((data) => (
              <Carousel.Item key={data.id}>
                <img className="img-fluid" src={data.path} alt=".." />
                <Carousel.Caption>
                  <h3>{data.title}</h3>
                  <p>{data.description}</p>
                </Carousel.Caption>
              </Carousel.Item>
            ))}
          </Carousel>
        </Col>
      </Row>
    </>
  );
};

export default About;
